export default {
	data() {
		return {
			diaryDetail: {}
		}
	},
	onLoad (options) {
		const poisList = uni.getStorageSync('diary_list');
		poisList.map((item) => {
			if (item.id === Number(options.id)) {
				this.diaryDetail = item;
			}
		})
		console.log(this.diaryDetail)
	},
	methods: {
		
	}
}
